<template>
  <div id="login-select">

    <!-- logo start -->
    <div class="logo">
      <img src="./images/logo.png" alt="">
    </div>
    <!-- logo end -->

    <!-- shop select start -->
    <div class="select-content">
      <div class="label">
        选择登陆店铺
      </div>

      <ul class="shop-content clearfix">
        <li 
        class="shop-item left" 
        v-for="(item, index) in shoplist" 
        :key="item.shop_id"
        @click="login(item.shop_id)"
        >
          <div class="logo-img">
            <img :src="imgbase_url + '/img_get.php?img=1&height=69&width=69&imgname=' + item.shop_logo" alt="店铺logo">
              <span class="shop-name">
                <span>
                  {{ item.shop_name}}
                </span>
              </span>
          </div>

        </li>
      </ul>
    </div>
    <!-- shopo select end -->

  </div>
  
</template>

<script>
import { loginShop } from "../api";
import { Public } from "@/config/public";
import { ls } from "@/config/pageStore";
import { errcode } from "@/config/cfg";

export default {
  data() {
    return {
      shoplist: [
        {
          shop_id: "4",
          shop_name: "金拱门",
          shop_logo: "b831c63e8bbb9ed9af2f878d09ce8be9.jpg"
        }
      ],
      imgbase_url: imgbase_url
    };
  },
  created() {
    this.init();
  },
  methods: {
    login(id) {
      let data = {
        login_shop: 1,
        shop_id: id
      };
      loginShop(data, resp => {
        if (resp.ret === 0) {
          ls.setItem("public#shopid", id);
          this.$router.push({ path: "/statistical" });
        } else if (resp.ret === errcode.EMPLOYEE_IS_FREEZE) {
          this.$notify({
            title: "警告",
            message: "该店铺本账号已经冻结",
            type: "warning"
          });
        } else if (resp.ret === errcode.USER_PERMISSION_ERR) {
          this.$notify({
            title: "警告",
            message: "操作权限不足",
            type: "warning"
          });
        }
      });
    },
    init() {
      this.shoplist = Public.getShopinfo().shopinfo || [];

      window.onstorage = v => {
        if (v.key === "public#shopid") {
          this.$notify({
            title: "警告",
            message: "在新标签打开其他店铺",
            type: "warning",
            duration: 0
            // onClose: () => {
            //   // this.closeWebPage();
            // }
          });
        }
      };
    },
    closeWebPage() {
      if (navigator.userAgent.indexOf("MSIE") > 0) {
        //close IE
        if (navigator.userAgent.indexOf("MSIE 6.0") > 0) {
          window.opener = null;
          window.close();
        } else {
          window.open("", "_top");
          window.top.close();
        }
      } else if (navigator.userAgent.indexOf("Firefox") > 0) {
        //close firefox
        window.location.href = "about:blank ";
      } else {
        //close chrome;It is effective when it is only one.
        window.opener = null;
        window.open("", "_self");
        window.close();
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

#login-select {
  width: 100%;
  height: 100vh;
  background-image: url(./images/loadingpage.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  .logo {
    position: absolute;
    @include wh(212px, 72px);
    @include tl(50px, 60px);

    img {
      @include wh(212px, 72px);
    }
  }

  .select-content {
    height: 80%;
    background-color: #fff;
    position: absolute;
    left: 40px;
    right: 40px;
    top: 16%;
    box-shadow: 0 2px 4px 0 #becaeb;
    border-radius: 2px;

    .label {
      position: absolute;
      @include wh(230px, 50px);
      @include fc(20px, #fff);
      @include tl(50px,-40px);
      background-color: #4877e7;
      border-radius: 0 25px 25px 0;
      font-weight: bold;
      line-height: 50px;
      text-align: right;
      padding-right: 20px;
    }
  }
}

.shop-content {
  margin-top: 140px;
  margin-left: 80px;
  margin-right: 80px;
}

.logo-img {
  @include wh(160px, 160px);
  border-radius: 2px;
  box-shadow: 0 4px 8px 0 #becaeb;
  cursor: pointer;

  img {
    @include wh(160px, 160px);
    border-radius: 2px;
  }
}
.logo-img:hover {
  box-shadow: none;
}
.shop-item {
  position: relative;
  text-align: center;
  @include fc(16px, #fff);
  margin-right: 20px;
}

.shop-name {
  cursor: pointer;
  padding: 8px 0;
  display: block;
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
  height: 60px;
  opacity: 0.5;
  background: #000000;

  span {
    display: block;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
}
</style>



